<template>
  <div class="d-flex">
    <button class="image-button" v-on:click="click(-0.1)"><img src="../assets/images/ic-dec.png"/></button>
    <input type="number" step="0.1" v-bind:value="formatedLevel" v-on:change="change" />
    <button class="image-button" v-on:click="click(0.1)"><img src="../assets/images/ic-inc.png"/></button>
  </div>
  
</template>

<script>

const MIN = -2.0
const MAX = 0.0

export default {
  name: 'AlarmList',
  model: {
    prop: "level",
    event: "change"
  },
  props: {
    level: Number,
  },
  data() {
    return {
      item: 0
    }
  },
  methods: {
    change: function ($event) {
      // var newVal = parseFloat($event.target.value)
      var newVal = Math.floor(parseFloat($event.target.value) * 10) / 10
      if(newVal < MIN) {
        newVal = MIN
        $event.target.value = MIN + ''
      } else if (newVal > MAX) {
        newVal = MAX
        $event.target.value = MAX + ''
      }

      this.$emit("change", newVal);
      this.item = newVal
    },
    click: function(step) {
      this.item += step;
      if(this.item < MIN) {
        this.item = MIN
      } else if (this.item > MAX) {
        this.item = MAX
      }
      this.$emit("change", this.item);
    }
  },
  computed: {
    formatedLevel() {
      return this.item.toFixed(1)
    }
  },
  watch: {
    level: function(newLevel) {
      this.item = newLevel
    }
  },
  mounted() {
    this.item = this.level;
  }
}
</script>

