      this.alarms2[11].status = newData.fid1141
      this.alarms2[12].status = newData.fid1142
      this.alarms2[13].status = newData.fid1143
      this.alarms2[14].status = newData.fid1150
      this.alarms2[15].status = newData.fid1151
      this.alarms2[16].status = newData.fid1152
      this.alarms2[17].status = newData.fid1153
    }
  },
  methods: {
    statusName(status, sru) {
      if (this.type && sru) {
        return 'Disabled'
      }

      switch(status) {
        case 0:
          return 'Normal'
        case 1:
          return 'Minor'
        case 2:
          return 'Major'
        case 3:
          return 'Critical'
        default:
          return 'Unknown'
      }
    },
    statusClass(status, sru) {
      if (this.type && sru) {
        return 'disabled'
      }
      switch(status) {
        case 0:
          return 'normal'
        case 1:
          return 'minor'
        case 2:
          return 'major'
        case 3:
          return 'critical'
        default:
          return 'disabled'
      }
    },
    txtColor(sru) {
      return (this.type && sru) ? 'txt-disabled' : 'txt-enable'
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
table {
  width: 330px;
  display: block;
  background: #FFFFFF;
  box-sizing: border-box;
  font-style: normal;
  font-weight: normal;
  font-size: 12px;
}

thead { 
  height: 32px;
  width: calc(100% - 4px); /* - 17px because of the scrollbar width */
  text-align: center;
  background: #ECEFF1;

  tr {
    td { border: 1px solid #CFD8DC; }
    td:nth-of-type(1) { width: 50px; }
    td:nth-of-type(2) { width: 198px; }
    td:nth-of-type(3) { width: 82px; }
  }
}

tbody {
  display: block;
  overflow: hidden;
  
  tr {
    height: 38px;
    box-sizing: border-box;
    td {
      border: 1px solid #CFD8DC;
      border-top: 0;
    }
    td:nth-of-type(1) {
      width: 50px;
      text-align: center;
    }
    td:nth-of-type(2) { 
      width: 198px;
    }
    td:nth-of-type(3) {
      width: 82px;
      text-align: center;
    }
  }

  tr:last-child {
    td {
      border: 1px solid #CFD8DC;
      border-top: 0;
    }
    td:nth-of-type(1) {
      width: 50px;
      text-align: center;
    }
    td:nth-of-type(2) { 
      width: 198px;
    }
    td:nth-of-type(3) {
      width: 82px;
      text-align: center;
    }
  }
}

.txt-disabled {
  color: #CECECE
}

.txt-enable {
  color: #263238;
}

tbody::-webkit-scrollbar {
  width: 4px;
  padding: 0px;
  margin: 0px;
}

tbody::-webkit-scrollbar-thumb {
  background-color: #2f3542;
  border-radius: 5px;
  background-clip: padding-box;
  border: 0px solid transparent;
}

tbody::-webkit-scrollbar-track {
  background-color: #ECEFF1;
  border-radius: 2px;
  box-shadow: inset 0px 0px 5px white;
}

.name-content {
  margin: auto 8px;
}

.image-button {
  width: 28px;
  height: 28px;
  border: none;
  background-color: transparent;
}

.mr-16 {
  margin-right: 16px;
}

.normal {
  color: white;
  background-color: #4CAF50;
}

.minor {
  color:#263238;
  background-color: #FFEB3B;
}

.major {
  color: white;
  background-color: #FF9800;
}

.critical {
  color: white;
  background-color: #F31C1C;
}

.disabled {
  color:#263238;
  background-color: #CFD8DC;
}

</style>
