<template>
  <div class="box">
    <v-navbar current="system" />
    <b-overlay :show="show" rounded="sm">
    <div class="d-flex">
      <!-- Alram Status -->
      <div class="d-block card-box alarm-status">
        <div class="d-flex align-items-center card-title">
          Alarm status
        </div>
        <div class="d-flex mt-3">
          <span class="subtitle1 alarm-led-label">Alarm</span>
          <div :class="alarmStatus.classAlarm">{{getAlarmStatus(alarmStatus.alarm)}}</div>
          <span v-if="isAdmin" class="subtitle1 alarm-mask-led-label">Alarm mask</span>
          <div v-if="isAdmin" :class="alarmStatus.classAlarmMAsk">{{(alarmStatus.alarmMask === 0) ? 'OFF' : 'ON'}}</div>
        </div>
        <div class="d-block box-current-alarm">
          <p class="current-alarm-label">Current Alarm</p>
          <v-alarms :type="devType" :data="alarmStatus.data" class="flex-fill alarm-list-gap" />
        </div>
      </div>
      <div class="d-block">
        <!-- Status -->
        <div class="d-block card-box status">
          <div class="d-flex align-items-center justify-content-between card-title">
            <span>Status</span>
            <span class="clock">{{currentTime}}</span>
          </div>
          <div class="d-block">
            <!-- RU version -->
            <div class="d-flex align-items-center version">
              <div class="label version-label">RU version</div>
              <div class="value version-value">{{trimRuVersion}}</div>
              <button id="popover-target-ru-version" class="image-button" v-show="this.status.ruVersion !== undefined && this.status.ruVersion !== null && this.status.ruVersion.length > 30">
                <img src="../assets/images/ic-info.png"/>
              </button>
              <!-- RU version popover -->
              <b-popover target="popover-target-ru-version" triggers="hover" placement="top">
