            </div>
            <!-- MT version -->
            <div class="d-flex align-items-center version">
              <div class="label version-label">MT version</div>
              <div class="value version-value">{{trimMtVersion}}</div>
              <button id="popover-target-mt-version" class="image-button" v-show="this.status.mtVersion !== undefined && this.status.mtVersion !== null && this.status.mtVersion.length > 30">
                <img src="../assets/images/ic-info.png"/>
              </button>
              <!-- MT version popover -->
              <b-popover target="popover-target-mt-version" triggers="hover" placement="top">
                {{status.mtVersion}}
              </b-popover>
            </div>
            <div class="d-flex align-items-center version">
              <!-- Admin state -->
              <div class="label label2">Admin-state</div>
              <div :class="status.classAdminState">{{getAdminState(status.adminState)}}</div>
              <!-- VLAN ID -->
              <div class="label label3">VLAN ID</div>
              <div class="value3">{{status.vlanid1}} ~ {{status.vlanid2}}</div>
            </div>
            <div class="d-flex align-items-center version2">
              <div class="label label2">Opt Link</div>
              <div :class="status.classOptLink">{{getLinkState(status.optLink)}}</div>
              <div class="label label3">RU Link</div>
              <div :class="status.classRuLink">{{getLinkState(status.ruLink)}}</div>
            </div>
          </div>
        </div>
        <!-- Control -->
        <div class="d-block card-box control">
          <div class="d-flex align-items-center justify-content-between card-title">
            <span>Control</span>
            <button class="btn-refresh" v-on:click="getControl()">Refresh</button>

          </div>
          <div class="d-block">
            <div class="d-flex box-admin-state" v-if="isAdmin">
              <span class="text-label">Admin-state</span>
              <b-dropdown id="admin-state" :text="getAdminState(control.adminState)" toggle-text="" right class="input-admin-state" split-class="custom-dropdown-split" toggle-class="custom-dropdown-button">
                <b-dropdown-item @click="control.adminState=1">Unlocked</b-dropdown-item>
                <b-dropdown-item @click="control.adminState=0">Locked</b-dropdown-item>
              </b-dropdown>
            </div>
            <div class="d-flex align-items-center box-admin-state">
                <span class="mr-16 text-label" v-if="isAdmin">Alarm masking On/Off</span>
                <v-switch v-model="control.alarmMasking" v-if="isAdmin" color="#303F9F" class="mr-48" />
                <span class="mr-16 text-label">Alarm test</span>
                <v-switch v-model="control.alarmTest" color="#303F9F"/>
            </div>
            <div class="d-flex align-items-center box-admin-state">
                <span class="mr-16 text-label">Tx 0 On/Off</span>
                <v-switch v-model="control.tx1.state" color="#303F9F"/>
                <span class="ml-48 mr-16 text-label">Tx 0 LVL</span>
                <v-spin v-model="control.tx1.level" />
                <button id="popover-target-tx-info" class="image-button"><img src="../assets/images/ic-info2.png"/></button>
                <b-popover target="popover-target-tx-info" triggers="hover" placement="bottom">
                  -2.0 ~ 0 with 0.1 step
                </b-popover>
            </div>
            <div class="d-flex align-items-center box-admin-state">
                <span class="mr-16 text-label">Tx 1 On/Off</span>
                <v-switch v-model="control.tx2.state" color="#303F9F"/>
                <span class="ml-48 mr-16 text-label">Tx 1 LVL</span>
                <v-spin v-model="control.tx2.level" />
            </div>
            <div class="d-flex align-items-center box-admin-state">
                <span class="mr-16 text-label">Tx 2 On/Off</span>
                <v-switch v-model="control.tx3.state" color="#303F9F"/>
                <span class="ml-48 mr-16 text-label">Tx 2 LVL</span>
                <v-spin v-model="control.tx3.level" />
            </div>
            <div class="d-flex align-items-center box-admin-state">
                <span class="mr-16 text-label">Tx 3 On/Off</span>
                <v-switch v-model="control.tx4.state" color="#303F9F"/>
                <span class="ml-48 mr-16 text-label">Tx 3 LVL</span>
                <v-spin v-model="control.tx4.level" />
            </div>
            <div class="d-flex align-items-center box-admin-state">
              <span class="mr-16 text-label">VLAN ID</span>
              <input v-model="control.vlanID.from" type="number" class="vlan" />
              <span class="mx-10">~</span>
              <input v-model="control.vlanID.to"  type="number" class="vlan" />
            </div>
            <!-- Upload setting from file -->
            <div class="d-flex align-items-center box-admin-state filebox">
              <span class="mr-16 text-label">Upload setting</span>
              <label for="ex_filename">Choose file</label>
              <input class="upload-name" v-bind:value="control.file" disabled="disabled">
              <input ref="file" type="file" accept=".txt, text/csv" id="ex_filename" class="upload-hidden" v-on:change="loadControlFile">
              <img v-if="control.file !== undefined" src="../assets/images/ic-checked.png"/>
              <img v-if="control.file === undefined" src="../assets/images/ic-unchecked.png"/>
            </div>
            <div class="d-flex justify-content-end box-admin-state mb-24">
              <button class="btn-save" v-on:click="saveToFile()">Save setting</button>
              <button class="btn-save" v-on:click="apply()">Apply</button>
            </div>
          </div>
        </div>
        <!-- Initialize & Reset -->
        <div class="d-flex justify-content-between mr-24 init" v-if="$store.getters.isAdmin">
          <div class="d-block card-box w-50 mr-24">
            <div class="d-flex align-items-center card-title">
              Initialization
            </div>
            <button class="btn-save ml-16 mt-14" v-b-modal.netconf-modal>NETCONF</button>
          </div>
          <div class="d-block card-box w-50">
            <div class="d-flex align-items-center card-title">
              Reset
            </div>
            <button class="btn-save ml-16 mt-14" v-b-modal.reset-modal>System</button>
          </div>
        </div>
      </div>
    </div>
    <div class="debug"> {{control}} </div>
    </b-overlay>

    <!-- begin - popup for create a new user -->
    <b-modal id="reset-modal" centered header-class="p-0 custom-modal-header" footer-class="p-0 custom-modal-footer" dialog-class="custom-modal-dialog2" content-class="custom-modal-content">
      <template #modal-header>
        <div class="modal-header">Reset</div>
      </template>
      
      <div class="d-block">
        <div class="d-flex justify-content-center">
          <img src="../assets/images/img-alert.png" class="mx-auto"/>
        </div>
        <div class="modal-desc">
          Device will be reset.
        </div>
      </div>

      <template #modal-footer>
        <div class="d-flex align-items-stretch modal-footer">
          <button @click="reset()" class="flex-fill modal-btn reset">
            Reset
          </button>
          <button @click="cancelReset()" class="flex-fill modal-btn cancel">
            Cancel
          </button>
        </div>
      </template>
    </b-modal>
    <!-- end - popup for create a new user -->
    <!-- begin - popup for create a new user -->
    <b-modal id="netconf-modal" centered header-class="p-0 custom-modal-header" footer-class="p-0 custom-modal-footer" dialog-class="custom-modal-dialog2" content-class="custom-modal-content">
      <template #modal-header>
        <div class="modal-header">Initialization</div>
      </template>
      
      <div class="d-block">
        <div class="d-flex justify-content-center">
          <img src="../assets/images/img-alert.png" class="mx-auto"/>
        </div>
        <div class="modal-desc">
          Device will be reset.
        </div>
      </div>

      <template #modal-footer>
        <div class="d-flex align-items-stretch modal-footer">
          <button @click="initialization()" class="flex-fill modal-btn reset">
            Initialize
          </button>
          <button @click="cancelReset()" class="flex-fill modal-btn cancel">
            Cancel
          </button>
        </div>
      </template>
    </b-modal>
    <!-- end - popup for create a new user -->
    <!-- begin - popup for create a new user -->
    <b-modal id="vlan-modal" centered header-class="p-0 custom-modal-header" footer-class="p-0 custom-modal-footer" dialog-class="custom-modal-dialog2" content-class="custom-modal-content">
      <template #modal-header>
        <div class="modal-header">Alert</div>
      </template>
      
      <div class="d-block">
        <div class="d-flex justify-content-center">
          <img src="../assets/images/img-alert.png" class="mx-auto"/>
        </div>
        <div class="modal-desc">
          VLAN ID1 can not be greater than ID2.
          <!-- Invalid value for VLAN ID.
          Please check the VLAN ID. -->
        </div>
      </div>

      <template #modal-footer>
        <div class="d-flex align-items-stretch modal-footer">
          <button @click="cancelReset()" class="flex-fill modal-btn cancel">
            Cancel
          </button>
        </div>
      </template>
    </b-modal>
    <!-- end - popup for create a new user -->
    <b-modal id="file-modal" centered header-class="p-0 custom-modal-header" footer-class="p-0 custom-modal-footer" dialog-class="custom-modal-dialog2" content-class="custom-modal-content">
      <template #modal-header>
        <div class="modal-header">Alert</div>
      </template>
      
      <div class="d-block">
        <div class="d-flex justify-content-center">
          <img src="../assets/images/img-alert.png" class="mx-auto"/>
        </div>
        <div class="modal-desc">
          Invalid file.
          <!-- Invalid value for VLAN ID.
          Please check the VLAN ID. -->
        </div>
      </div>

      <template #modal-footer>
        <div class="d-flex align-items-stretch modal-footer">
          <button @click="cancelReset()" class="flex-fill modal-btn cancel">
            Cancel
          </button>
        </div>
      </template>
    </b-modal>
    <!-- end - popup for create a new user -->
  </div>
</template>

<script>
import Navbar from '../components/Navbar.vue'
import CurrentAlarm from '../components/CurrentAlarm.vue'
import SwitchButton from '../components/SwitchButton.vue'
import SpinInput from '../components/SpinInput.vue'
import axios from 'axios'

const getControllFileName = () => {
  var now = new Date()
  var year = now.getFullYear()
  var month = ('0' + (now.getMonth() + 1)).slice(-2)
  var day = ('0' + now.getDate()).slice(-2)
  var hours = ('0' + now.getHours()).slice(-2)
  var minutes = ('0' + now.getMinutes()).slice(-2)
  var seconds = ('0' + now.getSeconds()).slice(-2)
  
  return 'ctrl-' + year + month + day + hours + minutes + seconds + '.txt';
}

const getControllFileContent = (control) => {
  var content = control.adminState
  content += '\n' + control.alarmMasking
  content += '\n' + control.alarmTest
  content += '\n' + control.tx1.state
  content += '\n' + control.tx2.state
  content += '\n' + control.tx3.state
  content += '\n' + control.tx4.state
  content += '\n' + control.tx1.level.toFixed(1)
  content += '\n' + control.tx2.level.toFixed(1)
  content += '\n' + control.tx3.level.toFixed(1)
  content += '\n' + control.tx4.level.toFixed(1)
  content += '\n' + control.vlanID.from
  content += '\n' + control.vlanID.to
  return content
}

const replaceAll = (str, searchStr, replaceStr) => {

   return str.split(searchStr).join(replaceStr);
}

export default {
  name: 'system',
  data() {
    return {
      show: true,
      devType: false,
      currentTime: '0000-00-00 00:00:00',
      clockInstance: undefined,
      ctrlChangeInstance: undefined,
      alarmStatus: {
        alarm: 0,
        classAlarm: 'status-led normal',
        alarmMask: 0,
        classAlarmMAsk: 'status-led off',
        data: {
          fid2: 0,
          fid4: 0,
          fid10: 0,
          fid11: 0,
          fid12: 0,
          fid17: 0,
          fid18: 0,
          fid23: 0,
          fid26: 0,
          fid28: 0,
          fid1001: 0,
          fid1002: 0,
          fid1020: 0,
          fid1030: 0,
          fid1040: 0,
          fid1050: 0,
          fid1060: 0,
          fid1100: 0,
          fid1101: 0,
          fid1102: 0,
          fid1103: 0,
          fid1110: 0,
          fid1111: 0,
          fid1112: 0,
          fid1113: 0,
          fid1120: 0,
          fid1121: 0,
          fid1122: 0,
          fid1123: 0,
          fid1140: 0,
          fid1141: 0,
          fid1142: 0,
          fid1143: 0,
          fid1150: 0,
          fid1151: 0,
          fid1152: 0,
          fid1153: 0,
        }
      },
      status : {
        adminState: 0,
        classAdminState: 'bg-disabled',
        ruVersion: '',
        mtVersion: '',
        optLink: 0,
        classOptLink: 'bg-disabled',
        ruLink: 0,
        classRuLink: 'bg-disabled',
        vlanid1: 0,
        vlanid2: 0
      },
      control: {
        adminState: 0, //0:locked 1:unlocked
        alarmMasking: 0,
        alarmTest: 0,
        tx1: {
          state: 0,
          level: 1.0
        },
        tx2: {
          state: 0,
          level: 1.0
        },
        tx3: {
          state: 0,
          level: 1.0
        },
        tx4: {
          state: 0,
          level: 1.0
        },
        vlanID: {
          from: 0,
          to: 0
        },
        file: undefined
      }
    }
  },
  created() {
    this.show = true;

    if (this.clockInstance !== undefined) {
      clearInterval(this.clockInstance)
    }

    this.clockInstance = setInterval(() => {
      this.getStatus()
    }, 1000);

    this.getControl()
  },
  destroyed() {
    if (this.clockInstance !== undefined) {
      clearInterval(this.clockInstance)
    }
  },
  mounted() {
  },
  computed: {
    trimRuVersion() {
      if (this.status === undefined || this.status.ruVersion === undefined || this.status.ruVersion === null || this.status.ruVersion.length === 0) {
        return ''
      }
      var lastIdx = (this.status.ruVersion.length <= 30) ? this.status.ruVersion.length : 30
      return (this.status.ruVersion.length > 30) ? this.status.ruVersion.substring(0, lastIdx) + '...' : this.status.ruVersion.substring(0, lastIdx)
    },
    trimMtVersion() {
      if (this.status === undefined || this.status.mtVersion === undefined || this.status.mtVersion === null || this.status.mtVersion.length === 0) {
        return ''
      }
      var lastIdx = (this.status.mtVersion.length <= 30) ? this.status.mtVersion.length : 30
      return (this.status.mtVersion.length > 30) ? this.status.mtVersion.substring(0, lastIdx) + '...' : this.status.mtVersion.substring(0, lastIdx)
    },
    isAdmin() {
      return this.$store.getters.isAdmin
    }
  },
  methods: {
    loadControlFile: function($event) {
      var reader = new FileReader();
      reader.onload = function (e) {
        var err = false
        var result = []
        var temp = 0
        var cleanStr = replaceAll(e.target.result,"\r\n", "\n")
        cleanStr = replaceAll(cleanStr,"\r", "\n")
        var cells = cleanStr.split("\n");

        if (cells.length === 13) {
          for (var i = 0; i < cells.length; i++) {
            switch(i) {
              case 0: // admin-state
              case 1: // alarm masking
              case 2: // alarmTest
              case 3: // tx1 on/off
              case 4: // tx2 on/off
              case 5: // tx3 on/off
              case 6: // tx4 on/off
                temp = parseFloat(cells[i]);
                if (temp === 1 || temp === 0) {
                  result.push(temp)
                } else {
                  err = true
                }
                break;
              case 7: // tx1 level
              case 8: // tx2 level
              case 9: // tx3 level
              case 10: // tx4 level
                temp = parseFloat(cells[i]);
                if (temp >= -2 && temp <= 0) {
                  result.push(temp)
                } else {
                  err = true
                }
                break;
              case 11: // vlan ID from
                temp = parseInt(cells[i]);
                if (temp >= 0 && temp <= 4096) {
                  result.push(temp)
                } else {
                  err = true
                }
                break;
              case 12: // vlan ID to
                temp = parseInt(cells[i]);
                if (temp > result[10] && temp <= 4096) {
                  result.push(temp)
                } else {
                  err = true
                }
                break;
            }
          }
        } else {
          err = true
        }

        if (!err) {
          if (this.$store.getters.isAdmin) {
            this.control.adminState = result[0]
            this.control.alarmMasking = result[1]
          }
          this.control.alarmTest = result[2]
          this.control.tx1.state = result[3]
          this.control.tx2.state = result[4]
          this.control.tx3.state = result[5]
          this.control.tx4.state = result[6]
          this.control.tx1.level = result[7]
          this.control.tx2.level = result[8]
          this.control.tx3.level = result[9]
          this.control.tx4.level = result[10]
          this.control.vlanID.from = result[11]
          this.control.vlanID.to = result[12]
          this.control.file = $event.target.files[0].name
        } else {
          this.$bvModal.show('file-modal')
          this.control.file = undefined
        }

        this.$refs.file.value = ''
      }.bind(this)

      reader.readAsText($event.target.files[0])
    },
    saveToFile: function() {
      if (this.control.vlanID.from > this.control.vlanID.to 
          || this.control.vlanID.from < 0 
          || this.control.vlanID.from > 4096
          || this.control.vlanID.to < 0 
          || this.control.vlanID.to > 4096) {
        this.$bvModal.show('vlan-modal')
      } else {
        var fileName = getControllFileName()
        var content = getControllFileContent(this.control);
        var blob = new Blob([content], { type: 'text/plain' });
        var objURL = window.URL.createObjectURL(blob);
      
        if (window.__Xr_objURL_forCreatingFile__) {
          window.URL.revokeObjectURL(window.__Xr_objURL_forCreatingFile__);
        }
        window.__Xr_objURL_forCreatingFile__ = objURL;
        var a = document.createElement('a');
        a.download = fileName;
        a.href = objURL;
        a.click();
        window.URL.revokeObjectURL(a); //메모리 누수 방지
      }
    },
    getAdminState: function(code) {
      return (code === 0) ? 'Locked' : 'Unlocked'
    },
    getLinkState: function(code) {
      return (code === 0) ? 'Disconnected' : 'Connected'
    },
    getControl: function() {
      axios({ // 세션키가 유효한지 서버에 조회한다.
        method: 'get',
        url: this.$store.getters.apiServer + '/php/control.php',
        params: {
          key: this.$store.getters.sessionKey,
          id: this.$store.getters.userid
        },
      }).then ((resp) => {
        if (resp.status == 200 && resp.data.state == 'Success') {
          const c = resp.data.data

          this.control.file = undefined
          this.control.adminState = c.admin_state
          this.control.alarmMasking = c.alarm_mask
          this.control.alarmTest = c.alarm_test
          this.control.vlanID.from = c.vlanid1
          this.control.vlanID.to = c.vlanid2
          this.control.tx1.state = c.tx1_onoff
          this.control.tx2.state = c.tx2_onoff
          this.control.tx3.state = c.tx3_onoff
          this.control.tx4.state = c.tx4_onoff
          this.control.tx1.level = c.tx1_lvl
          this.control.tx2.level = c.tx2_lvl
          this.control.tx3.level = c.tx3_lvl
          this.control.tx4.level = c.tx4_lvl

          if(c.change === 0 || c.change === 2) {
            this.show = false;
          } else {
            if (this.ctrlChangeInstance !== undefined) {
              clearTimeout(this.ctrlChangeInstance);
            }

            this.ctrlChangeInstance = setTimeout(this.getControl, 1000);
          }

        } else {
          if (this.ctrlChangeInstance !== undefined) {
            clearTimeout(this.ctrlChangeInstance);
          }
          this.ctrlChangeInstance = setTimeout(this.getControl, 1000);
        }
      }).catch(err => {
        console.error(err);
        if (this.ctrlChangeInstance !== undefined) {
          clearTimeout(this.ctrlChangeInstance);
        }
        this.ctrlChangeInstance = setTimeout(this.getControl, 1000);
      })
    },
    getStatus: function() {
      axios({ // 세션키가 유효한지 서버에 조회한다.
        method: 'get',
        url: this.$store.getters.apiServer + '/php/status.php',
        params: {
          key: this.$store.getters.sessionKey,
          id: this.$store.getters.userid
        },
      }).then ((resp) => {
        if (resp.status == 200 && resp.data.state == 'Success') {
          const s = resp.data.data

          this.devType = (s.devType === 1)
          this.currentTime = s.date
          this.status.ruVersion = s.ru_ver
          this.status.mtVersion = s.mt_ver
          this.status.adminState = s.admin_state
          this.status.classAdminState = (s.admin_state === 0) ? 'bg-disabled' : 'bg-enabled'
          this.status.optLink = s.opt_link
          this.status.classOptLink = (s.opt_link === 0) ? 'bg-disabled' : 'bg-enabled'
          this.status.ruLink = s.ru_link
          this.status.classRuLink = (s.ru_link === 0) ? 'bg-disabled' : 'bg-enabled'
          this.status.vlanid1 = s.vlanid1
          this.status.vlanid2 = s.vlanid2

          this.alarmStatus.alarm = s.alarm
          //
          switch(s.alarm) {
            case 0:
              this.alarmStatus.classAlarm = 'status-led normal'
              break;
            case 1:
              this.alarmStatus.classAlarm = 'status-led minor'
              break;
            case 2:
              this.alarmStatus.classAlarm = 'status-led major'
              break;
            case 3:
              this.alarmStatus.classAlarm = 'status-led critical'
              break;
            default:
              this.alarmStatus.classAlarm = 'status-led normal'
              break;
          }
          this.alarmStatus.alarmMask = s.alarm_mask
          if (s.alarm_mask === 0) {
            this.alarmStatus.classAlarmMAsk = 'status-led off'
          } else {
            this.alarmStatus.classAlarmMAsk = 'status-led on'
          }
          var data = {}
          data.fid2 = s.fid2
          data.fid4 = s.fid4
          data.fid10 = s.fid10
          data.fid11 = s.fid11
          data.fid12 = s.fid12
          data.fid17 = s.fid17
          data.fid18 = s.fid18
          data.fid23 = s.fid23
          data.fid26 = s.fid26
          data.fid28 = s.fid28
          data.fid1001 = s.fid1001
          data.fid1002 = s.fid1002
          data.fid1020 = s.fid1020
          data.fid1030 = s.fid1030
          data.fid1040 = s.fid1040
          data.fid1050 = s.fid1050
          data.fid1060 = s.fid1060
          data.fid1100 = s.fid1100
          data.fid1101 = s.fid1101
          data.fid1102 = s.fid1102
          data.fid1103 = s.fid1103
          data.fid1110 = s.fid1110
          data.fid1111 = s.fid1111
          data.fid1112 = s.fid1112
          data.fid1113 = s.fid1113
          data.fid1120 = s.fid1120
          data.fid1121 = s.fid1121
          data.fid1122 = s.fid1122
          data.fid1123 = s.fid1123
          data.fid1140 = s.fid1140
          data.fid1141 = s.fid1141
          data.fid1142 = s.fid1142
          data.fid1143 = s.fid1143
          data.fid1150 = s.fid1150
          data.fid1151 = s.fid1151
          data.fid1152 = s.fid1152
          data.fid1153 = s.fid1153
          this.alarmStatus.data = data

        }
      }).catch(err => {
        console.error(err);
      })
    },
    getAlarmStatus: function(status) {
      switch(status) {
        case 0:
          return 'Normal'
        case 1:
          return 'Minor'
        case 2:
          return 'Major'
        case 3:
          return 'Critical'
        default:
          return 'Unknown'
      }
    },
    apply() {
      if (this.control.vlanID.from > this.control.vlanID.to 
          || this.control.vlanID.from < 0 
          || this.control.vlanID.from > 4096
          || this.control.vlanID.to < 0 
          || this.control.vlanID.to > 4096) {
        this.$bvModal.show('vlan-modal')
      } else {
        this.show = true;
        axios({
          method: 'put',
          url: this.$store.getters.apiServer + '/php/control.php',
          data: {
            admin_state: this.control.adminState,
            alarm_mask: this.control.alarmMasking,
            alarm_test: this.control.alarmTest,
            tx1_lvl: this.control.tx1.level,
            tx1_onoff: this.control.tx1.state,
            tx2_lvl: this.control.tx2.level,
            tx2_onoff: this.control.tx2.state,
            tx3_lvl: this.control.tx3.level,
            tx3_onoff: this.control.tx3.state,
            tx4_lvl: this.control.tx4.level,
            tx4_onoff: this.control.tx4.state,
            vlanid1: this.control.vlanID.from,
            vlanid2: this.control.vlanID.to
          },
          params: {
            key: this.$store.getters.sessionKey,
            id: this.$store.getters.userid
          },
        }).then(resp => {
          if (!(resp.data.data.change === 0 || resp.data.data.change === 2)) {
            if (this.ctrlChangeInstance !== undefined) {
              clearTimeout(this.ctrlChangeInstance);
            }
            this.ctrlChangeInstance = setTimeout(this.getControl, 1000);
          }
        }).catch(err => {
          console.err(err)
          this.show = false;
        })
      }
    },
    reset: function() {
      this.$bvModal.hide('reset-modal')
      axios({
          method: 'put',
          url: this.$store.getters.apiServer + '/php/reset.php',
          params: {
            key: this.$store.getters.sessionKey,
            id: this.$store.getters.userid
          },
        }).then(resp => {
          if (resp.status == 200 && resp.data.state == 'Success') {
            this.show = true;
            this.$store.commit('logout')
            
            setTimeout(() => {
              this.$router.push('Login')
            }, 2000)
          } else {
            console.log(resp.data)
          }
        }).catch(err => {
          console.err(err)
          this.show = false;
        })
    },
    cancelReset: function() {
      this.$bvModal.hide('file-modal')
      this.$bvModal.hide('reset-modal')
      this.$bvModal.hide('netconf-modal')
      this.$bvModal.hide('vlan-modal')
    },
    initialization: function() {
      this.$bvModal.hide('netconf-modal')
      axios({
          method: 'put',
          url: this.$store.getters.apiServer + '/php/netconf.php',
          params: {
            key: this.$store.getters.sessionKey,
            id: this.$store.getters.userid
          },
        }).then(resp => {
          if (resp.status == 200 && resp.data.state == 'Success') {
            this.show = true;
            this.$store.commit('logout')
            
            setTimeout(() => {
              this.$router.push('Login')
            }, 2000)
          } else {
            // TODO: 에러처리
          }
        }).catch(err => {
          console.err(err)
          this.show = false;
        })
    }
  },
  components: {
    'v-navbar': Navbar,
    'v-alarms': CurrentAlarm,
    'v-switch': SwitchButton,
    'v-spin': SpinInput
  }
}
</script>

